<template>
    <div id="box-message">
        <div class="container">
            <div class="col-md-8"></div>
            <div class="col-md-4">
                <transition name="box-message">
                    <div v-if="statusBoxMessage" class="box box-warning direct-chat direct-chat-warning">
                        <div class="box-header with-border">
                            <h3 class="box-title">
                                <img class="direct-chat-img" v-bind:src="boxMessageData.avata ? api+'/image/avata/'+boxMessageData.avata : '/tizi/img/avata-default.jpg'" alt="message user image">
                                <span class="box-username">
                                    <span class="username">{{ boxMessageData.username }}</span>
                                </span>
                            </h3>
                            <router-link v-if="boxMessageData.id_product != 0 && boxMessageData.product_name" :to="{
                                name:'route.product', 
                                params:{
                                    province: boxMessageData.product_url_province, 
                                    district: boxMessageData.product_url_district, 
                                    category: boxMessageData.product_url_category, 
                                    id: boxMessageData.id_product, 
                                    product: boxMessageData.product_url 
                                }
                            }">
                                <div id="box-message-product">
                                    <div class="img">
                                        <img :src="`${api}/image/product/thumbnail/${boxMessageData.product_image}`">
                                    </div>
                                    <div class="about">
                                        <h2 class="name">{{ boxMessageData.product_name }}</h2>
                                        <div class="bottom">
                                            <h3 class="category">{{ boxMessageData.product_category }}</h3>
                                            <h3 v-if="boxMessageData.product_price != 0" class="price">{{ number_format(boxMessageData.product_price,0) }} đ</h3>
                                            <h3 v-else class="price">Thoả thuận</h3>
                                        </div>
                                    </div>
                                </div>
                            </router-link>
                            <span v-if="boxMessageData.id_product != 0 && !boxMessageData.product_name" id="box-message-product">(Sản phẩm này đã bị xoá)</span>
                            <div class="box-tools pull-right">
                                <button v-on:click="hiddenBoxMessage" type="button" class="btn btn-box-tool" data-widget="remove"><i class="fa fa-times"></i>
                                </button>
                            </div>
                        </div>
                        <div class="box-body" >
                            <div class="direct-chat-messages" id="croll-to-end">
                                <div v-for="item in boxMessageContent" :key="item.id" :class="{'direct-chat-msg':true, 'right': isSend(item.id_user1)}" id="content-message">
                                    <div class="direct-chat-info clearfix">
                                        <span :class="{
                                            'direct-chat-name':true, 
                                            'pull-right': isSend(item.id_user1)}">
                                            {{ isSend(item.id_user1) ? getUser.username : boxMessageData.username }}
                                        </span>
                                        <span :class="{
                                            'direct-chat-timestamp': true, 
                                            'pull-left': isSend(item.id_user1), 
                                            'pull-right': !isSend(item.id_user1)}">
                                            {{ getTime(item.created_at).fromNow() }}
                                        </span>
                                    </div>
                                    <img v-if="isSend(item.id_user1)" class="direct-chat-img" v-bind:src="getUser.avata ? api+'/image/avata/'+getUser.avata : '/tizi/img/avata-default.jpg'" >
                                    <img v-if="!isSend(item.id_user1)" class="direct-chat-img" v-bind:src="boxMessageData.avata ? api+'/image/avata/'+boxMessageData.avata : '/tizi/img/avata-default.jpg'" >
                                    <div class="direct-chat-text">
                                        {{ item.content }}
                                    </div>
                                </div>
                            </div>
                        </div>
                        <!-- /.box-body -->
                        <div class="box-footer">
                            <div v-on:click="viewMessage(boxMessageContent)" class="input-group">
                                <input v-model="message"  v-on:keyup.enter="sendMessage" type="text" name="message" placeholder="Nội dung..." class="form-control" autocomplete="off">
                                <span class="input-group-btn">
                                    <button v-on:click="sendMessage(null)" type="button" class="btn btn-warning btn-flat">Gửi</button>
                                </span>
                            </div>
                        </div>
                        <!-- /.box-footer-->
                    </div>
                </transition>
            </div>
        </div>
    </div>
</template>

<script>
import {mapGetters} from "vuex";
import axios from 'axios';
import moment from "moment";

export default {
    data() {
        return {
            api: this.$store.state.domainApi,
            message:""
        }
    },
    computed: {
        ...mapGetters(['socket','statusBoxMessage','boxMessageData','boxMessageContent','getUser'])
    },
    methods: {
        number_format( number, decimals, dec_point, thousands_sep ) {
            var n = number, c = isNaN(decimals = Math.abs(decimals)) ? 2 : decimals;
            var d = dec_point == undefined ? "," : dec_point;
            var t = thousands_sep == undefined ? "." : thousands_sep, s = n < 0 ? "-" : "";
            var i = parseInt(n = Math.abs(+n || 0).toFixed(c)) + "", j = (j = i.length) > 3 ? j % 3 : 0;
                                    
            return s + (j ? i.substr(0, j) + t : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + t) + (c ? d + Math.abs(n - i).toFixed(c).slice(2) : "");
        },
        getTime(date){
            var time = new moment(date);
            return time;
        },
        hiddenBoxMessage(){
            this.$store.state.boxMessage.isBoxMessage = false;
        },
        isSend(id_user){
            return this.getUser.id==id_user;
        },
        sendMessage(e){
            if(this.message || e.target.value){
                var created_at = new Date();
                created_at = created_at.toISOString();

                var data={
                    id_product: this.$store.state.boxMessage.data.id_product,
                    id_user1: this.$store.state.user.id,
                    id_user2: this.$store.state.boxMessage.data.id_user,
                    created_at: created_at,
                    content: e ? e.target.value : this.message,
                    status:0
                }
                
                axios.post(this.api+"/auth/message", data)
                .then(response=>{
                    if(response.data.success) this.socket.emit("send_message", data);
                    else this.flash(response.data.message, "error", {timeout:3000});
                });
                this.$store.state.boxMessage.content.push(data);
                this.message = "";
            }
        },
        viewMessage(messages){
            //xem tin nhan
            var item = messages.length ? messages[messages.length-1] : {};
            if(this.$store.state.user.id != item.id_user1 && messages.length > 0 && item.status==0){
                this.$store.state.boxMessage.content[messages.length-1].status = 1;
                axios.get(this.api+"/auth/message/view-message",{
                    headers:{ Authorization: `Bearer ${this.$store.state.token}` },
                    params:{ id_product: item.id_product, id_user1: item.id_user1}
                })
                .then(response=>{
                    if(response.data.success)
                    this.$store.state.countNewMessage--;
                });
            }
        },
        getMessages(){
            axios.get(this.api+"/auth/message/all",{
                headers:{ 
                    Authorization: `Bearer ${this.$store.state.token}` 
                },
                params:{
                    page: this.page,
                    pageSize: this.pageSize
                }
            })
            .then(async response=>{
                var messages = response.data.messages;   
                this.pageCount = Math.ceil(response.data.count/this.pageSize);

                var arrayIdUser = this.getArrayIdUser(messages);
                var resArrayUser = await axios.get(this.api+"/auth/message/about-user",{
                    params:{
                        id_user: arrayIdUser
                    }
                });
                resArrayUser = resArrayUser.data;

                var arrayIdProduct = this.getArrayIdProduct(messages);
                var resArrayProduct = await axios.get(this.api+"/auth/message/about-product",{
                    params:{
                        id_product: arrayIdProduct
                    }
                });
                resArrayProduct = resArrayProduct.data;
                
                messages.forEach(async (e,index)=>{
                    var key_user = resArrayUser.findIndex(el=>(el.id==e.id_user1 || el.id==e.id_user2));
                    messages[index].people = resArrayUser[key_user];

                    var key_product = resArrayProduct.findIndex(el=>el.id==e.id_product);
                    
                    messages[index].product = resArrayProduct[key_product];
                });

                this.messages = messages;
            });
        },
        scrollToEnd(){
            var boxMessage = document.getElementById("croll-to-end");
            boxMessage.scrollTop = boxMessage.scrollHeight;
        }
    },
    created() {
        moment.locale("vi");
    },
    updated() {
        this.scrollToEnd();
    },
}
</script>

<style scoped>
#box-message{
    position: fixed;
    bottom: -20px;
}
#box-message .box{
    box-shadow: 0px 5px 7px 0px black;
}
#box-message .direct-chat-messages{
    height: 320px;
}

.box-message-enter-active {
    animation: box-message-in 0.4s forwards;
}
.box-message-leave-active {
    animation: box-message-out 0.4s forwards;
}
@keyframes box-message-in{
    0%{
        transform: translateY(50px);
        opacity: 0;
    }
    
    100%{
        transform: translateY(0px);
        opacity: 1;
    }
}
@keyframes box-message-out{
    0%{
        transform: translateY(0px);
        opacity: 1;
    }
    
    100%{
        transform: translateY(50px);
        opacity: 0;
    }
}

#box-message .box-title{
    display: inline-flex;
}
#box-message .box-username{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 5px;
}

#box-message-product{
    display: flex;
}
#box-message-product .img{
    position: relative;
}
#box-message-product .img img{
    width: 80px;
    height: 70px;
    object-fit: cover;
    border-radius: 3px;
}
#box-message-product .about{
    display: flex;
    flex-direction: column;
    margin-left: 5px;
}
#box-message-product .about .name{
    margin: 0px;
    font-size:15px;
    font-weight: 600;
    height: 32px;
    overflow: hidden;
}
#box-message-product .about .bottom{
    display: flex;
    flex-direction: column;
    margin-top: 5px;
}
#box-message-product .about .bottom .category{
    margin: 0px;
    font-size: 13px;
    font-weight: 600;
    color: #009688cf;
}
#box-message-product .about .bottom .price{
    margin: 5px 0px 0px 0px;
    font-size: 13px;
    font-weight: 600;
    color: red;
}
.box-tools.pull-right{
    position: absolute;
    right: 10px;
    top: 13px;
}
</style>