<template>
<li class="dropdown notifications-menu">
    <a v-on:click="toggleNotification(false)" class="dropdown-toggle" data-toggle="dropdown">
        <i class="fa fa-bell-o"></i>
        <span v-if="countNewNotification" class="label label-warning">{{ countNewNotification }}</span>
    </a>
    <transition name="menuheader">
        <ul v-if="isNotification" class="dropdown-menu" style="display:block;">
            <li class="header">10 thông báo gần nhất <a v-on:click="toggleNotification(false)" class="tizi-btn-hidden">x</a></li>
            <li>
                <ul class="menu">
                    <li v-on:click="toggleNotification(item)" v-for="item in notification" :key="item.id">
                        <router-link :to="item.url" :class="{'n-content': true, 'active': item.status}">
                            {{ item.content }}
                        </router-link>
                    </li>
                    <li v-if="notification.length==0">
                        <a style="color:black;">Không có thông báo nào !</a>
                    </li>
                </ul>
            </li>
            <li v-on:click="toggleNotification(false)" class="footer"><router-link :to="{name: 'user.notification'}">Xem tất cả</router-link></li>
        </ul>
    </transition>
</li>
</template>

<script>
import axios from "axios";
import {mapGetters} from "vuex";
export default {
    data() {
        return {
            api: this.$store.state.domainApi,
            notification:[]
        }
    },
    methods: {
        toggleNotification(item){
            if(this.$store.state.token){
                this.$store.state.isNotification = !this.isNotification;
                if(this.isNotification){
                    this.$store.state.isMessage = false;
                    this.getNotification();
                }
                if(item && item.status==0) axios(this.api+"/auth/notification/view",{
                    headers:{ 
                        Authorization: `Bearer ${this.$store.state.token}` 
                    },
                    params:{
                        id_notification: item.id
                    }
                })
                .then(()=>{
                    this.$store.state.countNewNotification--;
                });
            }else{
                this.$store.commit('getRoute', this.$route);// lich trang truoc khi login
                this.$router.push({name: 'login'});
            }
        },
        getNotification(){
            axios.get(this.api+"/auth/notification",{
                headers:{ 
                    Authorization: `Bearer ${this.$store.state.token}` 
                }
            })
            .then(response=>{
                this.notification = response.data.notification;
            });
        }
    },
    computed: {
        ...mapGetters(['isNotification','countNewNotification'])   
    }
}
</script>

<style>
    .n-content{
        font-weight: 600;
        color: #3F51B5 !important;
    }
    .n-content.active{
        font-weight: 500;
         color: black !important;
    }
    .dropdown-menu .tizi-btn-hidden{
        display: inline-block !important;
        position: absolute;
        right: 0;
        top: 1px;
        font-weight: 600;
        color: black !important;
        padding: 7px 26px;
    }
</style>