<template>
<li class="dropdown user user-menu" id="dangtin">
    <!-- Menu Toggle Button -->
    <a href="#" class="dropdown-toggle" data-toggle="dropdown">
        <button class="btn-dt btn-warning" v-on:click="show">Đăng tin</button>
    </a>
    <div :class="{ modal: true, fade: true, in: true ,show: dangtin }">
        <div class="modal-dialog">
            <transition name="slide-fade">
            <div v-if="modalContent" class="modal-content">
                <form @submit.prevent="handleSubmit">
                    <div class="modal-header">
                        <button type="button" class="close" v-on:click="hide">
                        <span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title">Đăng tin nhanh</h4>
                    </div>
                    <div class="modal-body">
                        <div class="form-group">
                            <label>Tỉnh thành</label>
                            <Select2 v-model="province" name="province" v-validate="'numeric'" 
                            :options="getProvinceByLv(0, { id:'chon-thanh-pho', text:'---Chọn thành phố---'})" 
                            @change="changeProvince($event)" class="input" id="_province"/>
                            <span class="error">{{ errors.first("province") }}</span>
                        </div>
                        <transition name="district">
                        <div v-if="province != 'chon-thanh-pho'" class="form-group">
                            <label>Quận huyện</label>
                            <Select2 v-model="district" name="district" v-validate="'numeric'"
                            :options="getProvinceByLv(province, { id: 'chon-quan-huyen', text:'---Chọn quận huyện---'})"
                            @change="changeDistrict($event)" class="input" id="_district"/>
                            <span class="error">{{ errors.first("district") }}</span>
                        </div>
                        </transition>
                        <div class="form-group">
                            <label>Địa chỉ</label>
                            <input v-model="product.address" name="address" v-validate="'required|min:5|max:255'" type="text" class="form-control input" placeholder="Số nhà, phường xã,..">
                            <span class="error">{{ errors.first("address") }}</span>
                        </div>
                        <div class="form-group">
                            <label>Chuyên mục</label>
                            <Select2 v-model="product.id_category" name="category" v-validate="'numeric'"
                            :options="getCategorySort(true)"
                            class="input" id="_category"/>
                            <span class="error">{{ errors.first("category") }}</span>
                        </div>
                        <div class="form-group">
                            <label>Tiêu đề tin</label>
                            <input v-model="product.name" name="name" v-validate="'required|min:5|max:60'" type="text" class="form-control input" placeholder="Vd: Điện thoại cũ giá rẻ">
                            <span class="error">{{ errors.first("name") }}</span>
                        </div>
                        <div class="form-group" style="position: relative;">
                            <label>Giá</label>
                            <input v-model="product.price" name="price" v-validate="'required|numeric|min_value:0|max:15'" type="text" class="form-control input" placeholder="Vd: 10000">
                            <span v-if="product.price > 0" class="format-price">{{ number_format(product.price, 0) }} đ</span>
                            <span v-if="product.price == 0" class="format-price">Thỏa thuận</span>
                            <span class="error">{{ errors.first("price") }}</span>
                        </div>
                        <div class="form-group">
                            <label>SDT</label>
                            <input v-model="product.phone" name="phone" v-validate="'required|numeric|min:10|max:10'" type="text" class="form-control input" placeholder="Nhập SDT...">
                            <span class="error">{{ errors.first("phone") }}</span>
                        </div>
                        <div class="box">
                            <div class="box-header">
                                <h3 class="box-title">Hình ảnh</h3>
                                <span class="error">{{ errors.first("image") }}</span>
                            </div>
                            <!-- /.box-header -->
                            <div class="box-body">
                                <div class="uploader">
                                    <div v-show="images.length" class="upload-controller">
                                        <label for="file" >Thêm hình</label>
                                    </div>
                                    <div v-show="!images.length" class="form-img">                                      
                                        <label for="file">Bấm vào đây chọn hình ảnh !</label>
                                        <input name="image" v-validate="'required|image'" ref="file" type="file" id="file" v-on:change="onInputChange" multiple>
                                    </div>
                                    <div v-show="images.length" class="images-preview">
                                        <div v-for="(image, index) in images" :key="index" class="img-wrapper">
                                            <div class="img" v-bind:style="{backgroundImage: 'url('+ image +')'}"></div>
                                            <a v-on:click="delImg(index)">xóa</a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="box">
                            <div class="box-header">
                                <h3 class="box-title">Nội dung</h3>
                                <span class="error">{{ errors.first("content") }}</span>
                            </div>
                            <!-- /.box-header -->
                            <div class="box-body">
                                <vue-editor v-model="product.content" name="content" v-validate="'required|min:5'" :editor-toolbar="customToolbar" />
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default pull-left" v-on:click="hide">Thoát</button>
                        <button type="submit" class="btn btn-primary">Đăng tin</button>
                    </div>
                </form>
            </div>
        </transition>
        <!-- /.modal-content -->
        </div>
        <!-- /.modal-dialog -->
    </div>
</li>
</template>


<script>
import axios from "axios";
import Select2 from 'v-select2-component';
import slug from "slug";
import { Validator } from 'vee-validate';
import { VueEditor } from "vue2-editor"; //<textarea v-model="product.content" name="content" v-validate="'required|min:5'" id="add-product-content"></textarea>

import {mapGetters} from "vuex";

export default {
    components: { Select2, VueEditor},
    data() {
        return {
            dangtin:false,
            modalContent:false,
            api: this.$store.state.domainApi,
            categoryMenu: false,
            product:{
                id_category: 'chon-chuyen-muc',
                id_province:0,
                id_user:0,
                name:"",
                url:"",
                address:"",
                price:0,
                phone:null,
                content:""
            },
            files:[],
            images:[],
            province: 'chon-thanh-pho',
            district: 'chon-quan-huyen',
            dictionary: {
                vi: {
                    custom: {
                        "province":{
                            numeric : "Vui lòng chọn tỉnh thành !"
                        },
                        "district":{
                            numeric : "Vui lòng chọn quận huyện !"
                        },
                        "address":{
                            required: "Vui lòng nhập địa chỉ !",
                            min: "Địa chỉ tối thiểu 5 ký tự !",
                            max: "Địa chỉ tối đa 255 ký tự !"
                        },
                        "category": {
                            numeric: "Vui lòng chọn chuyên mục !"
                        },
                        'name' : {
                            required: 'Vui lòng nhập tiêu đề tin !',
                            min: 'Tiêu để tin tổi thiểu 5 ký tự !',
                            max: 'Tiêu đề tin tối đa 60 ký tự !'
                        },
                        "price": {
                            required: "Vui lòng nhập giá !",
                            numeric: "Giá phải là số và không có đấu cách !",
                            min_value: "giá tối thiểu bằng 0",
                            max: "Giá tối đa 15 sô theo chiều ngang !"
                        },
                        "phone": {
                            required: "Vui lòng nhập SDT !",
                            numeric: "SDT phải là số và không có đấu cách !",
                            min: "SDT phải là 10 số",
                            max: "SDT phải là 10 số"
                        },
                        "image":{
                            required: "Vui lòng chọn hình ảnh !",
                            image: "Có file không đúng định dạng ảnh, đã loại bỏ !"
                        },
                        "content":{
                            required: 'Vui lòng nhập nội dung !',
                            min: 'Nôị dung tổi thiểu 5 ký tự !'
                        }
                    }
                }
            },
            customToolbar: [
                ['bold', 'italic', 'underline'],
                [{ 'list': 'ordered'}, { 'list': 'bullet' }],
                [{ 'align': ''},{ 'align': 'justify'}, { 'align': 'right' }],
                ['link', 'video']
            ]
        }
    },
    methods: {
        show () {
            // this.$store.commit('getRoute', this.$route);
            // if(this.$store.state.token){
            //     if(this.$store.state.user.status){
            //         this.dangtin = true;
            //         this.modalContent = true;
            //         document.querySelector("body").classList.add('modal-open');
            //     }else this.$swal.fire({
            //         title: 'Vui lòng xác thực tài khoản !',
            //         text: 'Sau một tuần các tài khoản chưa xác thực sẽ bị xoá !',
            //         icon: 'warning',
            //         showCancelButton: true,
            //         confirmButtonColor: '#3085d6',
            //         cancelButtonColor: '#d33',
            //         cancelButtonText: 'Thoát',
            //         confirmButtonText: 'Xác thực ngay'
            //     }).then((result) => {
            //         if (result.value) this.$router.push({name: 'user.confirm'});
            //     });
            // }else this.$swal.fire({
            //     title: 'Vui lòng đăng nhập !',
            //     icon: 'warning',
            //     showCancelButton: true,
            //     confirmButtonColor: '#3085d6',
            //     cancelButtonColor: '#d33',
            //     cancelButtonText: 'Thoát',
            //     confirmButtonText: 'Đăng nhập'
            // }).then((result) => {
            //     if (result.value) this.$router.push({name: 'login'});
            // });
            this.dangtin = true;
            this.modalContent = true;
            document.querySelector("body").classList.add('modal-open');
        },
        hide () {
            this.modalContent = false;
            document.querySelector("body").classList.remove('modal-open');
            setTimeout(() => {
                this.dangtin = false;
            }, 400);
        },
        number_format( number, decimals, dec_point, thousands_sep ) {
            var n = number, c = isNaN(decimals = Math.abs(decimals)) ? 2 : decimals;
            var d = dec_point == undefined ? "," : dec_point;
            var t = thousands_sep == undefined ? "." : thousands_sep, s = n < 0 ? "-" : "";
            var i = parseInt(n = Math.abs(+n || 0).toFixed(c)) + "", j = (j = i.length) > 3 ? j % 3 : 0;
                                    
            return s + (j ? i.substr(0, j) + t : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + t) + (c ? d + Math.abs(n - i).toFixed(c).slice(2) : "");
        },
        changeProvince(){
            this.district = 'chon-quan-huyen';
        },
        changeDistrict(id){
            this.product.id_province = id;
        },
        onInputChange(e){
            const files = e.target.files;
            Array.from(files).forEach(file => this.addImage(file));
        },
        addImage(file){
            if(!file.type.match('image.*')){
                console.log(`${file.name} is not an image`);
                return;
            }
            this.files.push(file);
            const reader = new FileReader();
            reader.onload = (e) => this.images.push(e.target.result);
            reader.readAsDataURL(file);
        },
        delImg(index){
            this.images.splice(index, 1);
            this.files.splice(index, 1);
            if(this.images.length == 0){
                this.$refs.file.value = null;
            }
        },
        handleSubmit() {
            let name = this.product.name;
            this.product.url = slug(name.toLowerCase());
            this.$validator.validate().then(valid => {
                if (valid) {
                    const formData = new FormData();
                    formData.set("id_province", this.product.id_province);
                    formData.append("id_category", this.product.id_category);
                    formData.append("id_user", this.$store.state.user.id ? this.$store.state.user.id : 0);
                    formData.append("name", this.product.name);
                    formData.append("url", this.product.url);
                    formData.append("address", this.product.address);
                    formData.append("price", this.product.price);
                    formData.append("phone", this.product.phone);
                    formData.append("content", this.product.content);
                    for(var file of this.files){
                        formData.append("images", file);
                    }

                    this.$swal.fire({
                        title: 'Đang đăng tin',
                        text: "Vui lòng chờ !",
                        allowOutsideClick: false
                    });
                    this.$swal.showLoading();
                    axios.post(`${this.api}/public/product`, formData)
                    .then(response => {
                        this.$swal.close();
                        if(response.data.success===1){

                            if(!this.$store.state.token && localStorage.id_product) {
                                var id_product = JSON.parse(localStorage.id_product);
                                id_product.push(response.data.id_product);
                                localStorage.id_product = JSON.stringify(id_product);
                            }else if(!this.$store.state.token) localStorage.id_product = JSON.stringify([response.data.id_product]);
                                      
                            this.flash(response.data.message, 'success', {
                                timeout: 3000
                            });
                            this.$validator.reset();
                            this.product.price = null;
                            this.product.name = '';
                            this.product.content = '';
                            this.files = [];
                            this.images = [];
                            
                            //Ẩn cửa sổ đăng tin
                            this.hide();
                            if(!this.$store.state.token)
                            this.$swal.fire({
                                title: 'Vui lòng đăng ký tài khoản để được duyệt tin!',
                                text: 'Nếu bạn đã có tài khoản thì hày đăng nhập!',
                                icon: 'warning',
                                showCancelButton: true,
                                confirmButtonColor: '#3085d6',
                                cancelButtonColor: '#d33',
                                cancelButtonText: 'Đăng nhập',
                                confirmButtonText: 'Đăng ký',
                                allowOutsideClick: false
                            }).then((result) => {
                                if (result.value) this.$router.push({name: 'register', query:{phone: this.product.phone}});
                                else this.$router.push({name: 'login', query:{phone: this.product.phone}});
                            });
                            
                        }else{
                            this.flash(response.data.message, 'error', {
                                timeout: 3000
                            });
                        }
                        
                    });
                }
            });
        }
    },
    computed:{
        ...mapGetters(['getProvinceByLv', 'getCategorySort'])
    },
    created() {
        Validator.localize(this.dictionary);
        const validator = new Validator({ first_name: 'alpha' });
        validator.localize('vi');
    },
}
</script>

<style>
