    width: 100%;
    height: 200px;
    padding: 15px;
    resize: none;
    border: 1px solid #d2d6de;
}
.modal.fade.in.show{
    z-index: 1050 !important;
    top:0;
}
.select2-dropdown {
    z-index: 1051;
}
