    padding: 2px 6px;
    border-radius: 1px;
    font-size: 17px;
}
.modal-body .form-group .error, .modal-body .box .box-header .error{
    color: red;
    margin-left: 113px;
}
.modal-body .form-group label{
    display: inline-block;
    width: 20%;
}
.modal-body .form-group .input{
    display: inline-block;
    width: 79%;
}
.modal-body .box{
    border: none;
    box-shadow: none;
    margin-bottom: 0px;
}
.modal-body .box .box-header{
    padding: 10px 0px;
}
.modal-body .box .box-header .box-title{
    font-weight: 600;
}
.modal-body .box .box-body{
    padding: 0px 0px 10px 0px;
}
.uploader {
    max-width: 768px;
    margin: 0 auto;
    width: 100%;
    background: #009688;
    padding: 15px 0px;
    text-align: center;
    outline: 2px dashed white;
    outline-offset: -10px;
    color: white;
    font-size: 25px;
    position: relative;
}
.uploader .form-img label{
    cursor: pointer;
}
.uploader .form-img input{
    display: none;
}
.images-preview {
    display: flex;
    flex-wrap: wrap;
    margin-top: 20px;
    padding: 15px 10px 0px 15px;
}
.images-preview .img-wrapper {
    width: 32%;
    display: flex;
    flex-direction: column;
    height: 150px;
    justify-content: space-between;
    background: #fff;
    margin-right: 7px;
    margin-bottom: 5px;
    padding: 10px;
    position: relative;
}
.images-preview .img-wrapper .img {
    width: 100%;
    height: 100%;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    outline: 2px dashed #00BCD4;
    outline-offset: 0px;
}
.images-preview .img-wrapper a{
    position: absolute;
    font-size: 15px;
    display: inline-block;
    padding: 5px 15px;
    background: #ff000069;
    color: white;
    cursor: pointer;
    border-radius: 2px;
    right: 11px;
}
.upload-controller {
    position: absolute;
    top: 10px;
    left: 16px;
}
.uploader .upload-controller label {
    background: #FF9800;
