    }
    
    100%{
        transform: translateY(-49px);
        height:0px;
        opacity: 0;
    }
}
@media (max-width: 991px) and (min-width: 376px){
    .nav>li>a {
        padding: 15px 11px;
    }
}
@media(max-width: 425px){
