<template>
 <li class="dropdown user user-menu">
    <a v-on:click="show" class="dropdown-toggle" data-toggle="dropdown">
        <img v-bind:src="($store.state.token && user.avata) ? api+'/image/avata/'+user.avata : '/tizi/img/avata-default.jpg'" class="user-image">
        <span class="hidden-xs"><span class="caret"></span></span>
    </a>
    <transition name="menuheader">
        <ul v-if="menu" class="dropdown-menu" style="display:block;">
            <li class="user-header" id="user-header-avatar">
                <img v-bind:src="user.avata ? api+'/image/avata/'+user.avata : '/tizi/img/avata-default.jpg'" class="img-circle" alt="User Image">
                <div class="user-header-about">
                    <h2>{{ user.username }}</h2>
                    <span>Số dư: <span class="account-balance">{{ number_format(user.account_balance,0) }}</span> đ</span>
                </div>
                <button v-on:click="hidden" type="button" class="btn-close close">×</button>
            </li>
            <li class="user-body">
                <ul class="nav nav-pills nav-stacked">
                    <li v-on:click="hidden">
                        <router-link  :to="{name: 'route.store',params:{store: user.url_store}}">
                            <i class="fa fa-archive"></i> Gian hàng của bạn
                        </router-link>
                    </li>
                    <li v-on:click="hidden">
                        <router-link  :to="{name: 'user.profile'}">
                            <i class="fa fa-user"></i> Thông tin tài khoản
                        </router-link>
                    </li>
                    <li v-on:click="hidden">
                        <router-link  :to="{name: 'user.product'}">
                            <i class="fa fa-th"></i> Quản lý sản phẩm
                        </router-link>
                    </li>
                </ul>
            </li>
            <li class="user-footer">
                <div v-if="user.name_group=='Admin'" class="pull-left">
                    <router-link to="/admin/home" class="btn btn-default btn-flat">Admin</router-link>
                </div>
                <div class="pull-right">
                    <a v-on:click="logout" class="btn btn-default btn-flat">Đăng xuất</a>
                </div>
            </li>
        </ul>
    </transition>
</li>   
</template>

<script>
import {mapGetters} from "vuex";

export default {
    data(){
        return {
            api: this.$store.state.domainApi,
            menu:false
        }
    },
    methods:{
        number_format( number, decimals, dec_point, thousands_sep ) {
            var n = number, c = isNaN(decimals = Math.abs(decimals)) ? 2 : decimals;
            var d = dec_point == undefined ? "," : dec_point;
            var t = thousands_sep == undefined ? "." : thousands_sep, s = n < 0 ? "-" : "";
            var i = parseInt(n = Math.abs(+n || 0).toFixed(c)) + "", j = (j = i.length) > 3 ? j % 3 : 0;
                                    
            return s + (j ? i.substr(0, j) + t : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + t) + (c ? d + Math.abs(n - i).toFixed(c).slice(2) : "");
        },
        show(){
            this.$store.commit('getRoute', this.$route);
            if(this.$store.state.token){
                this.menu = true;
                if(this.$store.state.user.status){
                    this.menu = true;
                }else this.$swal.fire({
                    title: 'Vui lòng xác thực tài khoản !',
                    text: 'Sau một tuần các tài khoản chưa xác thực sẽ bị xoá !',
                    icon: 'warning',
                    showCancelButton: true,
                    confirmButtonColor: '#3085d6',
                    cancelButtonColor: '#d33',
                    cancelButtonText: 'Thoát',
                    confirmButtonText: 'Xác thực ngay'
                }).then((result) => {
                    if (result.value) this.$router.push({name: 'user.confirm'});
                });
            }else this.$router.push({name: 'login'});
            // if(this.$store.state.token) this.menu = true;
            // else this.$swal.fire({
            //     title: 'Vui lòng đăng nhập !',
            //     icon: 'warning',
            //     showCancelButton: true,
            //     confirmButtonColor: '#3085d6',
            //     cancelButtonColor: '#d33',
            //     cancelButtonText: 'Thoát',
            //     confirmButtonText: 'Đăng nhập'
            // }).then((result) => {
            //     this.$store.commit('getRoute', this.$route);// lich trang truoc khi login
            //     if (result.value) this.$router.push({name: 'login'});
            // });
        },
        hidden(){
            this.menu = false;
        },
        logout(){
            this.socket.emit("user_logout",  this.$store.state.user);
            this.$store.state.token = false;
            this.$store.state.user = {};
            this.$store.state.countProductFollow = 0;
            this.$store.state.countNewMessage = 0;
            localStorage.removeItem('token');
            this.hidden();
            this.$router.push({name: 'home'});
        }
    },
    computed:{
        ...mapGetters({user: 'getUser', socket: 'socket'})
    }
}
</script>

