   padding: 0px;
   margin: 0px 0px 0px 10px;
   align-content: center;
}
#user-header-avatar .user-header-about h2{
    color: white;
    margin: 0px;
    font-size: 22px;
    text-align: left;
}
#user-header-avatar .user-header-about span{
   color: white;
}
