<template>
	<router-view />
</template>

<script>
import {mapGetters} from "vuex";
import axios from "axios";

export default {
	data(){
		return {
			api: this.$store.state.domainApi
		}
	},
	computed: {
		...mapGetters(['socket','getUser'])
	},
	methods: {
		connect(){
			this.socket.emit("user_connect", localStorage.token);
		},
		receive_message(){
			this.socket.on("receive_message", async data=>{
				if(this.$store.state.boxMessage.isBoxMessage==false && this.$store.state.boxMessage.data.id_product != data.id_product){
					var responseAboutUser = await axios.get(this.api+"/auth/message/about-user",{
						params:{
							id_user: [data.id_user1==this.$store.state.user.id ? data.id_user2 : data.id_user1]
						}
					});
					if(data.id_product){
						var aboutProduct = await axios.get(this.api+"/public/product/"+data.id_product);
					
						this.$store.state.boxMessage.data = {
							id_product:data.id_product,
							product_name: aboutProduct.data.about.name,
							product_category: aboutProduct.data.about.category,
							product_url: aboutProduct.data.about.url,
							product_url_category: aboutProduct.data.about.url_category,
							product_url_province: aboutProduct.data.about.url_province,
							product_url_district: aboutProduct.data.about.url_district,
							product_image: aboutProduct.data.about.id_group_image,
							product_price: aboutProduct.data.about.price,
							id_user: responseAboutUser.data[0].id,
							username: responseAboutUser.data[0].username,
							avata: responseAboutUser.data[0].avata
						}
					}else{
						this.$store.state.boxMessage.data = {
							id_product:data.id_product,
							id_user: responseAboutUser.data[0].id,
							username: responseAboutUser.data[0].username,
							avata: responseAboutUser.data[0].avata
						}
					}
					
					
					axios.get(this.api+"/auth/message",{
						params:{
							id_product: data.id_product,
							id_user1: data.id_user1,
							id_user2: data.id_user2
						}
					})
					.then(response=>{
						this.$store.state.boxMessage.content=response.data;
						this.$store.state.boxMessage.isBoxMessage = true;
					});
					
				}else if(this.$store.state.boxMessage.isBoxMessage==false && this.$store.state.boxMessage.data.id_product==data.id_product){
					this.$store.state.boxMessage.content.push(data);
					this.$store.state.boxMessage.isBoxMessage=true;
				}else if(this.$store.state.boxMessage.data.id_product==data.id_product){
					this.$store.state.boxMessage.content.push(data);
				}else this.flash("Có tin nhắn mới!", "success", {timeout:3000});

				//cap so luong nhat tin nhan
				axios.get(`${this.api}/auth/message/count-new-message`,{headers:{ Authorization: `Bearer ${this.$store.state.token}` }})
				.then(res=>{
					this.$store.state.countNewMessage = res.data.count_message;
				});
			});
		},
		notification(){
			this.socket.on("notification", data=>{
				this.flash(data.content, "success",{timeout:3000});
				this.$store.state.countNewNotification++;
			});
		},
		user_online(){
			this.socket.on("online", data=>{
                this.$store.state.online = data;
            });
		},
		users_online(){
			this.socket.on("users_online", data=>{
				this.$store.state.users_online = data;
			});
		}
	},
	mounted(){
		this.receive_message();
		this.$store.dispatch('loadData');
		this.socket.on("thong_bao", data=>{
			this.flash(data.message, "success", {timeout:3000});
		});
		this.notification();
		this.user_online();
		this.users_online()
	}
}
</script>