import Vue from "vue";
import VueRouter from "vue-router";
import store from '../store';
import {scroller} from 'vue-scrollto/src/scrollTo'
const ScrollTo = scroller();

import middlewarePipeline from './middlewarePipeline';

import route_public from "./public";
import route_admin from "./admin";

Vue.use(VueRouter);

const routes = [route_admin,route_public];

const router = new VueRouter({
    mode: "history",
    base: process.env.BASE_URL,
    routes,
    scrollBehavior() {
        ScrollTo("#app");
    }
});

router.beforeEach((to, from, next) => {
    const middleware = to.matched.reduce((results, obj)=>{
        if(obj.meta.middleware) return results = results.concat(obj.meta.middleware);
        return results;
    },[]);
    if(middleware.length == 0) return next();
    
    const context = {
        to,
        from,
        next,
        store
    }
    return middleware[0]({
        ...context,
        next: middlewarePipeline(context, middleware, 1)
    })
})
export default router;
