<template>
<li  class="dropdown messages-menu" id="tizi-message">
    <a v-on:click="open" class="dropdown-toggle" data-toggle="dropdown">
        <i class="fa fa-envelope-o"></i>
        <span v-if="countNewMessage" class="label label-success">{{ countNewMessage }}</span>
    </a>
    <transition name="menuheader">
        <ul v-if="isMessage" class="dropdown-menu" style="display:block;">
            <li class="header">15 cuộc trò chuyện mới <a v-on:click="open" class="tizi-btn-hidden">x</a></li>
            <li>
                <ul class="menu">
                    <li v-for="item in messages" :key="item.id">
                        <a v-on:click="openBoxMessage(item)">
                            <div class="pull-left">
                                <img v-bind:src="item.people.avata ? api+'/image/avata/'+item.people.avata : '/tizi/img/avata-default.jpg'" class="img-circle" :title="item.people.username">
                            </div>
                            <h4>
                                {{ item.people.username }}
                                <small><i class="fa fa-clock-o"></i> {{ getTime(item.created_at).fromNow() }}</small>
                            </h4>
                            <span v-if="item.product" class="message-product">({{ item.product.name }})</span>
                            <span v-if="!item.product && item.id_product" class="message-product">(Sản phẩm này đã bị xoá)</span>
                            <p :class="{'item-message-content':true, 'active': item.status}"><span v-if="$store.state.user.id==item.id_user1">Bạn: </span>{{ item.content }}</p>
                        </a>
                    </li>
                    <li v-if="messages.length==0">
                        <a style="color:black;">Không có cuộc trò chuyện nào !</a>
                    </li>
                </ul>
            </li>
            <li class="footer" v-on:click="open"><router-link :to="{name: 'user.message'}">Xem tất cả cuộc trò chuyện</router-link></li>
        </ul>
    </transition>
</li>
</template>

<script>
import axios from "axios";
import moment from "moment";
moment.locale("vi");
import {mapGetters} from "vuex";

export default {
    data(){
        return {
            api: this.$store.state.domainApi,
            messages:[],
            user:[]
        }
    },
    methods: {
        open(){
            if(this.$store.state.token){
                this.$store.state.isMessage = !this.isMessage;
                if(this.isMessage){
                    this.$store.state.isNotification = false;
                    this.getMessages();
                }
            }else{
                this.$store.commit('getRoute', this.$route);// lich trang truoc khi login
                this.$router.push({name: 'login'});
            }
        },
        close(){
            this.isMessage=false;
        },
        async openBoxMessage(item){
            // biên data chua thông tin cua nguoi đôi diên đang trò chuyện
            var data = {};
            if(item.id_product && item.product){
                data = {
                    id_product: item.id_product,
                    product_name: item.product.name,
                    product_category: item.product.category,
                    product_url: item.product.url,
                    product_url_category: item.product.url_category,
                    product_url_province: item.product.url_province,
                    product_url_district: item.product.url_district,
                    product_image: item.product.image,
                    product_price: item.product.price,
                    id_user: item.people.id,
                    username: item.people.username,
                    avata: item.people.avata
                };
            }else{
                data = {
                    id_product: item.id_product,
                    id_user: item.people.id,
                    username: item.people.username,
                    avata: item.people.avata
                };
            }
            
            this.$store.state.boxMessage.data = data;

            var messages = await axios.get(this.api+"/auth/message",{
                params:{
                    id_product:data.id_product,
                    id_user1: data.id_user,
                    id_user2: this.$store.state.user.id 
                }
            });
            this.$store.state.boxMessage.content = messages.data;
            this.$store.state.boxMessage.isBoxMessage = true;
            this.open();

            if(this.$store.state.user.id != item.id_user1 && item.status==0)
            this.viewMessage(item.id_product, item.id_user1);
        },
        viewMessage(id_product, id_user1){
            this.$store.state.boxMessage.content[this.$store.state.boxMessage.content.length - 1].status = 1;
            axios.get(this.api+"/auth/message/view-message",{
                headers:{ Authorization: `Bearer ${this.$store.state.token}` },
                params:{ id_product: id_product, id_user1: id_user1}
            })
            .then(response=>{
                if(response.data.success)
                this.$store.state.countNewMessage--;
            });
        },
        getTime(date){
            var time = new moment(date);
            return time;
        },
        getMessages(){
            axios.get(this.api+"/auth/message/all",{
                headers:{ 
                    Authorization: `Bearer ${this.$store.state.token}` 
                },
                params:{
                    page: this.page,
                    pageSize: this.pageSize
                }
            })
            .then(async response=>{
                var messages = response.data.messages;   
                this.pageCount = Math.ceil(response.data.count/this.pageSize);

                var arrayIdUser = this.getArrayIdUser(messages);
                var resArrayUser = await axios.get(this.api+"/auth/message/about-user",{
                    params:{
                        id_user: arrayIdUser
                    }
                });
                resArrayUser = resArrayUser.data;

                var arrayIdProduct = this.getArrayIdProduct(messages);
                var resArrayProduct = await axios.get(this.api+"/auth/message/about-product",{
                    params:{
                        id_product: arrayIdProduct
                    }
                });
                resArrayProduct = resArrayProduct.data;
                
                messages.forEach((e,index)=>{
                    var key_user = resArrayUser.findIndex(el=>(el.id==e.id_user1 || el.id==e.id_user2));
                    messages[index].people = resArrayUser[key_user];

                    var key_product = resArrayProduct.findIndex(el=>el.id==e.id_product);
                    
                    messages[index].product = resArrayProduct[key_product];
                });
                
                this.messages = messages;
            });
        },
        getArrayIdUser(data){
            return data.reduce((res,obj)=>{
                if(obj.id_user1==this.$store.state.user.id) res.push(obj.id_user2);
                else res.push(obj.id_user1);
                return res;
            },[]);
        },
        getArrayIdProduct(data){
            return data.reduce((res,obj)=>{
                if(res.findIndex(id_product=>id_product==obj.id_product)==-1 && obj.id_product != 0) res.push(obj.id_product);
                return res;
            },[]);
        }
    },
    computed: {
        ...mapGetters(['countNewMessage','isMessage'])
    }
}
</script>

<style>
#tizi-message .dropdown-menu .header{
    display: flex;
}
#tizi-message .dropdown-menu .header .tizi-btn-hidden{
    display: inline-block !important;
    position: absolute;
    right: 0;
    top: 1px;
    font-weight: 600;
    color: black;
    padding: 7px 26px;
}
.item-message-content{
    overflow: hidden;
    font-weight: 600;
    color: black !important;
}
.item-message-content.active{
    color: #6f7377 !important;
    font-weight: 500;
}
.message-product{
    font-size: 11px;
    display: block;
    overflow: hidden;
    color: #009688;
    font-weight: 600;
}
</style>