<template>
    <header class="main-header">
        <nav class="navbar navbar-static-top">
            <div id="_header" class="container">
                <div class="navbar-header">
                    <router-link to="/" class="navbar-brand"><b>Tizi.vn</b></router-link>
                </div>
                <LeftMenu />
                <RightMenu />
                <BoxMessage />
            </div>
        </nav>
        <div v-if="$store.state.user.id_group==2" class="users_online">
            <span class="user">User:{{ online().user }} </span>
            <span class="customer">Customer:{{ online().customer }} </span>
        </div>
    </header>
</template>

<script>
import RightMenu from "./right/Menu";
import LeftMenu from "./left/Menu";
import BoxMessage from "../BoxMessage";
import {mapGetters} from "vuex";
export default {
    components: {RightMenu,LeftMenu,BoxMessage},
    methods: {
        online(){
            var user = this.users_online.length;
            var customer = this.users_online.find(e=>e.about.id==0);
            if(customer){
                user--;
                customer = customer.sockets.length;
            }
            return {user: user, customer: customer};
        }
    },
    computed: {
        ...mapGetters(['users_online'])   
    }
}
</script>
<style>
#_header{
    position: relative;
}
.menuheader-enter-active {
    animation: menuheader-in 0.4s forwards;
}
.menuheader-leave-active {
    animation: menuheader-out 0.4s forwards;
}
@keyframes menuheader-in{
    0%{
        transform: translateY(-50px);
        opacity: 0;
    }
    
    100%{
        transform: translateY(0px);
        opacity: 1;
    }
}
@keyframes menuheader-out{
    0%{
        transform: translateX(0px);
        opacity: 1;
    }
    
    100%{
        transform: translateY(-50px);
        opacity: 0;
    }
}
.users_online{
    text-align: center;
    margin-top: 9px;
}
.users_online .user{
    padding: 10px;
