<template>
    <div class="form-group" id="header-search">
        <input v-model="searchValue" v-on:keyup.enter="search" type="text" class="form-control" id="navbar-search-input" placeholder="Tìm: Bất động sản, Đồ điện tử, Xe cộ,...">
        <button v-on:click="searchSubmit" class="header-search"><i class="fa fa-search"></i></button>
    </div>
</template>
<script>
export default {
    data(){
        return {
            searchValue:''
        }
    },
    methods:{
        search(event){ 
            if(this.$route.query.search != event.target.value.trim()){
                this.redirect(event.target.value.trim());
            }
        },
        searchSubmit(){ 
            if(this.$route.query.search != this.searchValue.trim()){
                this.redirect(this.searchValue.trim());
            }
        },
        redirect(search=null){
            this.searchValue = '';
            this.$router.push({ 
                name: "category", 
                params: { 
                    province: 'toan-quoc', 
                    category: 'tat-ca-danh-muc' 
                }, 
                query: {
                    search: search
                }
            });
        }
    }
}
</script>
<style>
#header-search{
    padding: 8px 0px;
    margin-bottom: 0px;
}
button.header-search {
    top: 12px;
}
input#navbar-search-input {
    width: 448px;
}
@media(max-width: 1199px){
    input#navbar-search-input {
        width: 256px;
    }
}
</style>